import React, { useEffect } from 'react'
import Select from "react-select";

function LocationFilter(props) {
    const category = [
        { value: 'set', label:''},
        { value: 'studio', label:''},
        { value: 'restaurant', label:''},
        { value: 'other', label:''},
        { value: 'qna', label: ''},
    ]
    
    useEffect(() => {
        props.setSubCategory(category[0].label);
    }, [])

    const selectStyles = {
        control: (styles, {isSelected}) => ({...styles, textAlign: 'center', width: '13rem', height: '1rem', margin: '0px', border:isSelected ? '1px solid #702cba' : null}),
        option: ({ isSelected }) => {
            return {
                width: '13rem',
                height: '2rem',
                lineHeight: '2rem',
                backgroundColor: isSelected
                ? 'rgba(17, 17, 17, 0.16)'
                : null,
                textAlign: 'center',
            }
        },
        singleValue: styles => ({
            ...styles,
            width:'13rem',
        }),
        menuList: styles => ({
            ...styles,
            padding: '0px',
        }),
        menu: styles => ({
            ...styles,
            margin: '0px'
        })
    }

    return (
        <>
        <div style={{display:"flex", flexWrap:"nowrap", alignItems:"center"}}>
            <p style={{marginBottom:"0px", marginRight:"1rem"}}></p>
            <Select options={category} defaultValue={category[0]} styles={selectStyles} onChange = {(e) => props.setSubCategory(e.label)}/>    
        </div>
        </>
    )
}

export default LocationFilter
